* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    max-width: 1202px;
    padding: 0 15px;
    margin: 15px auto 100px;

}

.header {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    column-gap: 15px;

    &__search {
        height: 42px;
        margin-bottom: 15px;
        border: none;
        border-radius: 15px;
        background: #dddada;
        padding-left: 15px;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        &:focus {
            outline: 0;
        }

        &::placeholder {
            text-indent: 10px;
            color: #514848;
            opacity: 0.6;
        }
    }

    &__boards {
        height: 42px;
        border: none;
        border-radius: 15px;
        background: #dddada;
        border-radius: 15px;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        padding-left: 15px;
    }
}

.pictures{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 30px;
    /* margin-bottom:115px */

    &__pic-box{
        position: relative;
            aspect-ratio: 0.8/1;
            overflow: hidden;
            margin-bottom: 2px;
    }

    &__avatar-box{
        overflow: hidden;
        width: 60px;
        height: 60px;
        border-radius: 50%;
    }
    &__avatar{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__discription{
        display: flex;
        column-gap: 20px;
    }
    &__tags{
        display: flex;
        align-items: center;
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        justify-content: center;
    }
}

.pic{
    width: 100%;
    height: 100%;
}

.complains {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(247, 142, 93, 0.8);
}

.complain{
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    margin-bottom: 20px;

    &__container{
        max-width: 500px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        background-color: rgba(205, 200, 198, 0.8); 
        margin: 15% auto;
        padding: 15px;
        border-radius: 15px;
        /* display: none; */
    }

    &__title{
        color: #000;
        font-family: Inter;
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        display: flex;
        justify-content: center;
    }

    &__item{
        display: flex; 
        column-gap: 30px;
        padding-left: 30px;
        /* position: relative; */
    }

    &__tit{
        color: #000;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    &__text{
        color: rgba(0, 0, 0, 0.61);
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__buttons{
        display: flex;
        justify-content: end;
        column-gap: 15px;
    }

