}

.photo-board {
    /* display: none; */
    display: flex;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(247, 142, 93, 0.8);
    visibility: hidden;
    opacity: 0;

    &__content {
        background-color: rgba(205, 200, 198, 0.8); 
        margin: 15% auto;
        padding: 15px;
        border: 1px solid black;
        max-width: 300px;
        border-radius: 15px;
    }
}

.picture__menu {
    position:absolute;
    background-color: rgba(81, 72, 72, 0.7);
    border: 1px solid rgb(240, 164, 177);
    border-radius: 5px;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hidden {
    opacity: 0;
    visibility: hidden;
}






